@use "sass:color";
@use "sass:map";
@use "sass:string";
@use "theme";
@use "../functions";

// Default General
// --------------------------------------------------

// Global font path
$font-path: "/dist/fonts" !default;

// Hairlines width
$hairlines-width: .55px !default;

// Layout columns
$columns: 12 !default;


// Radius
// --------------------------------------------------

$radius-value: 1em !default;
$radiuses: (
  (
    0: 0,
    1: $radius-value * .4,
    2: $radius-value * .75,
    3: $radius-value,
    4: $radius-value * 1.5,
    5: $radius-value * 2
  )
) !default;

$radius: var(--bkkr-border-radius, #{$radius-value}) !default;


// Shadows
// --------------------------------------------------

$shadow-color-value:          theme.$text-color-rgb !default;
$shadow-color:                var(--box-shadow-color, #{$shadow-color-value}) !default;

$shadow-x:                    0 !default;
$shadow-y:                    6px !default;
$shadow-radius:               10px !default;
$shadow-spread:               0 !default;

$shadows: (
  (
    0:  none,
    1:  #{
          $shadow-x*.5 $shadow-y*.25 $shadow-radius*.25 $shadow-spread * -0.5 rgba(#{$shadow-color}, .1),
          $shadow-x*.5 $shadow-y $shadow-radius*0.5 $shadow-spread rgba(#{$shadow-color}, .12),
          $shadow-x*.5 $shadow-y*.1 $shadow-radius $shadow-spread rgba(#{$shadow-color}, .08)
        },
    2:  #{
          $shadow-x $shadow-y*.5 $shadow-radius*.5 $shadow-spread * -1 rgba(#{$shadow-color}, .1),
          $shadow-x $shadow-y $shadow-radius $shadow-spread rgba(#{$shadow-color}, .12),
          $shadow-x $shadow-y*.2 $shadow-radius*2 $shadow-spread rgba(#{$shadow-color}, .08)
        },
    3:  #{
          $shadow-x*2 $shadow-y*2.5 $shadow-radius*2.5 $shadow-spread * -2 rgba(#{$shadow-color}, .1),
          $shadow-x*2 $shadow-y $shadow-radius*2 $shadow-spread rgba(#{$shadow-color}, .12),
          $shadow-x*2 $shadow-y*2.2 $shadow-radius*4 $shadow-spread rgba(#{$shadow-color}, .08)
        },
  )
) !default;

$shadow-value:                map.get($shadows, 2) !default;
$shadow:                      var(--bkkr-box-shadow, #{$shadow-value}) !default;


// Transition presets
// --------------------------------------------------

// Easing curves
$ease-cubic:          cubic-bezier(.32, .72, 0, 1) !default;
$ease-bounce:         cubic-bezier(.155, 1.105, .295, 1.12) !default;
$ease-bounce-button:  cubic-bezier(.25, 1.11, .78, 1.59) !default;

// Transition presets
$transition-base:     all .3s $ease-cubic !default;
$transition-fade:     opacity .15s linear !default;
$transition-collapse: height .35s ease !default;


// Spacing
// --------------------------------------------------
// Control the default styling of most BKKR elements by modifying these
// variables. Mostly focused on spacing.
// You can add more entries to the $spacers map, should you need more variation.

$spacer-value: 16px !default;
$spacers: (
  (
    0: 0,
    1: $spacer-value * .25,
    2: $spacer-value * .5,
    3: $spacer-value,
    4: $spacer-value * 1.5,
    5: $spacer-value * 3,
    auto: auto
  )
) !default;

$spacer: var(--bkkr-spacer, #{$spacer-value}) !default;


// Sizes
// --------------------------------------------------
// This variable affects the `.h-*` and `.w-*` classes.

$sizes: (
  (
    25:     25%,
    50:     50%,
    75:     75%,
    100:    100%,
    auto:   auto
  )
) !default;


// Z-Index
// --------------------------------------------------
// Grouped by elements which would be siblings

$z-indexes: (
  (
    menu:             1000,
    overlay:          1001,
    overlay-wrapper:  10,
    click-block:      99999,
    fixed:            999,
    scroll:           1,
    page:             0,
    toolbar:          10,
    backdrop:         2,
    input:            2
  )
) !default;


// Typography
// --------------------------------------------------
// Font, line-height, and color for body text, headings, and more.

// stylelint-disable value-keyword-case
$font-family-sans-serif: system-ui, -apple-system, "Segoe UI", Roboto, "Helvetica Neue", Arial, "Noto Sans", sans-serif,
  "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol", "Noto Color Emoji" !default;
$font-family-monospace: SFMono-Regular, Menlo, Monaco, Consolas, "Liberation Mono", "Courier New", monospace !default;
// stylelint-enable value-keyword-case

$font-family-base: var(--default-font-sans-serif) !default;
$font-family-code: var(--default-font-monospace) !default;


// $font-size-root effects the value of `rem`, which is used for as well font sizes, paddings and margins
// $font-size-base effects the font size of the body text
$font-size-root: null !default;
$font-size-base: 1rem !default; // Assumes the browser default, typically `16px`

$font-weight-lighter: 100 !default;
$font-weight-light: 300 !default;
$font-weight-normal: 400 !default;
$font-weight-semibold: 500 !default;
$font-weight-bold: 700 !default;
$font-weight-bolder: 900 !default;

$font-weight-base: $font-weight-normal !default;

$line-height-base: 1.5 !default;

// Heading properties
$headings: (
  h1: $font-size-base * 3.5,
  h2: $font-size-base * 1.825,
  h3: $font-size-base * 1.375,
  h4: $font-size-base * 1.15,
  h5: $font-size-base * 1,
  h6: $font-size-base * 1
) !default;

$headings-margin-bottom: $spacer-value / 2 !default;
$headings-font-family: inherit !default;
$headings-font-style: inherit !default;
$headings-font-weight: 500 !default;
$headings-line-height: 1.2 !default;
$headings-color: inherit !default;
$headings-opacity: 1 !default;

// Lead
$lead-font-size: ($font-size-base * 1.25) !default;
$lead-font-weight: 300 !default;


// Paragraphs
$paragraph-margin-bottom: 1rem !default;

// Links
$link-color: theme.$primary !default;
$link-decoration: none !default;
$link-hover-color: color.adjust($link-color, $lightness: -15%) !default;
$link-hover-decoration: underline !default;

// Legend
$legend-margin-bottom: .5rem !default;
$legend-font-size: 1.5rem !default;
$legend-font-weight: null !default;

// Mark
$mark-padding: .2em !default;
$mark-bg: #fcf8e3 !default;

// Blockquote
$blockquote-small-color: theme.$background-color-step-600 !default;
$blockquote-font-size: ($font-size-base * 1.25) !default;

// Text utilities
$small-font-size: .875em !default;
$sub-sup-font-size: .75em !default;
$text-muted: theme.$background-color-step-600 !default;
$dt-font-weight: $font-weight-bold !default;
$placeholder-text-color: var(--bkkr-placeholder-color, theme.$text-color-step-600) !default;
$overlay-background-color: var(--bkkr-overlay-background-color, #fafafa) !default;

// Hr
$hr-margin-y: $spacer !default;
$hr-color: var(--bkkr-border, rgba(theme.$text-color-rgb, .1)) !default;
$hr-height: $hairlines-width !default;

// Tables
// --------------------------------------------------
// Customizes the `.table` component with basic values, each used across all table variations.

$table-cell-padding-y: .5rem !default;

$table-cell-padding-x:        .5rem !default;
$table-cell-padding-y-sm:     .25rem !default;
$table-cell-padding-x-sm:     .25rem !default;

$table-cell-vertical-align:   top !default;
$table-caption-color: $text-muted !default;
$table-th-font-weight: null !default;
