@use "sass:color" as sass-color;
@use "../functions";

// Default Theme
//
// This file contains the theme variables. This should only include variables
// used to theme the application colors.


// Default theme colors
//
// Color map should provide
// - base: main color to be used.
// - contrast: Color that will provide readable text against the base color
// - shade: 12% darker version of the base color (sass-color.mix with black)
// - tint: 10% lighter version of the base color (sass-color.mix with white)

$primary:         #3880ff !default;
$secondary:       #3dc2ff !default;
$tertiary:        #5260ff !default;
$success:         #2dd36f !default;
$warning:         #ffc409 !default;
$danger:          #eb445a !default;
$light:           #f4f5f8 !default;
$medium:          #92949c !default;
$dark:            #222428 !default;

$colors: (
  primary: (
    base:             $primary,
    contrast:         #fff,
    shade:            functions.color-get-shade($primary),
    tint:             functions.color-get-tint($primary)
  ),
  secondary: (
    base:             $secondary,
    contrast:         #fff,
    shade:            functions.color-get-shade($secondary),
    tint:             functions.color-get-tint($secondary)
  ),
  tertiary: (
    base:             $tertiary,
    contrast:         #fff,
    shade:            functions.color-get-shade($tertiary),
    tint:             functions.color-get-tint($tertiary)
  ),
  success: (
    base:             $success,
    contrast:         #fff,
    shade:            functions.color-get-shade($success),
    tint:             functions.color-get-tint($success)
  ),
  warning: (
    base:             $warning,
    contrast:         #000,
    shade:            functions.color-get-shade($warning),
    tint:             functions.color-get-tint($warning)
  ),
  danger: (
    base:             $danger,
    contrast:         #fff,
    shade:            functions.color-get-shade($danger),
    tint:             functions.color-get-tint($danger)
  ),
  light: (
    base:             $light,
    contrast:         #000,
    shade:            functions.color-get-shade($light),
    tint:             functions.color-get-tint($light)
  ),
  medium: (
    base:             $medium,
    contrast:         #fff,
    shade:            functions.color-get-shade($medium),
    tint:             functions.color-get-tint($medium)
  ),
  dark: (
    base:             $dark,
    contrast:         #fff,
    shade:            functions.color-get-shade($dark),
    tint:             functions.color-get-tint($dark)
  )
) !default;


// Default foreground and background colors

$background-color-value:        #fff !default;
$background-color-rgb-value:    255, 255, 255 !default;
$text-color-value:              #000 !default;
$text-color-rgb-value:          0, 0, 0 !default;

$background-color:              var(--bkkr-background-color, #{$background-color-value}) !default;
$background-color-rgb:          var(--bkkr-background-color-rgb, #{$background-color-rgb-value}) !default;
$text-color:                    var(--bkkr-text-color, #{$text-color-value}) !default;
$text-color-rgb:                var(--bkkr-text-color-rgb, #{$text-color-rgb-value}) !default;


// Default Foreground and Background Step Colors
// -------------------------------------------------------------------------------------------
// Color Steps are used to provide variations in text and background colors of elements.
// Steps move towards their contrasting color.
// For example, $text-color-step-XXX will be $text-color stepping towards $background-color,
// but a $background-color-step-XXX will be $background-color stepping towards $text-color.

$background-color-step-50:          var(--bkkr-color-step-50, sass-color.mix($text-color-value, $background-color-value, 5%)) !default;
$background-color-step-100:         var(--bkkr-color-step-100, sass-color.mix($text-color-value, $background-color-value, 10%)) !default;
